import React from 'react'
import Canvas from './Canvas'
import './Styles/MainSite.css'
import UnderlinedTitle from './UnderlinedTitle'

const MainSite = () => {

    return (
        <main className="container main-site" id="start">
            <UnderlinedTitle text="Start" />
            <div className="clearfix">
                <div className="canvas-div">
                    <Canvas name="mainsite"/>
                </div>
                <div className="start-information">
                    <p>Implement a draggable joystick easily on your own videogames!. Written in VanillaJS and easy to customize for your own designs. Have a look at the code and implement it in two easy steps.</p>
                </div> 
            </div>
        </main>
    )
}

export default MainSite